<template>
    <div>
        <div class="stats-con" v-show="userRole>0">
            <div class="steps" style="width: 1000px">
                <div class="card-title-con" style="font-size: 20px;margin-top: -20px;margin-left: 20px;">
                    
                    <el-date-picker
                            style="margin-left: 20px"
                            v-model="whichYear1"
                            align="right"
                            type="year"
                            placeholder="">
                    </el-date-picker>
                    <el-button type="primary" @click="selectPersonDataByYear"></el-button>
                </div>
                <div class="steps-body">
                    <div id="myChart1" :style="{width:'600px',height:'400px',left:'40px',float:'left'}"></div>
                    <div style="width: 300px;margin-top: 100px" v-if="personData">
                        <ol>
                            <li class="my-li"><span class="fb-span">:</span><span>{{personData.taskNum}}</span></li>
                            <li class="my-li"><span class="fb-span">:</span><span>{{personData.taskTime}}</span></li>
                            <!--<li class="my-li"><span class="fb-span">:</span><span>{{personData.vacationCount}}</span></li>-->
                            <!--<li class="my-li"><span class="fb-span">:</span><span>{{personData.vacationTime}}</span></li>-->
                            <li class="my-li"><span class="fb-span">:</span><span>{{personData.singleTaskNum}}</span></li>
                            <li class="my-li"><span class="fb-span">:</span><span>{{personData.multiTaskNum}}</span></li>
                            <li><span class="fb-span">:</span><br/>
                                <a style="cursor: pointer;color: #69C8FA;text-decoration: underline" @click="fetchTaskDetail(personData.mostTimeTaskId,1)">{{personData.mostTimeTaskName}}</a></li>
                            <li><span class="fb-span">:</span><br/>
                                <a style="cursor: pointer;color: #69C8FA;text-decoration: underline" @click="fetchTaskDetail(personData.lessTimeTaskId,0)">{{personData.lessTimeTaskName}}</a></li>
                        </ol>
                    </div>
                </div>
            </div>
        </div>
        <div class="stats-con" v-show="userRole>0">
            <div class="steps" style="width: 1000px">
                <div class="card-title-con" style="font-size: 20px;margin-top: -20px;margin-left: 20px;">
                    
                    <el-date-picker
                            style="margin-left: 20px"
                            v-model="whichYear2"
                            align="right"
                            type="year"
                            placeholder="">
                    </el-date-picker>
                    <el-button type="primary" @click="selectVacationByYear"></el-button>
                </div>
                <div class="steps-body" style="height: 600px;margin-left: 120px">
                    <div id="myChart2" :style="{width:'600px',height:'400px',left:'80px',float:'left',marginTop:'50px'}"></div>
                    <div style="width: 300px;margin-left: 100px">
                        <ol type="A" style="margin-left: -250px">
                            <li><span class="task-span">:</span><span style="margin-left: 71px">{{vacationData.totalCount}}</span></li>
                            <li><span class="task-span">:</span><span style="margin-left: 71px">{{vacationData.totalTime}}</span></li>
                        </ol>
                    </div>
                </div>
                <!--<div v-else class="steps-body" style="height: 300px">
                    <h1 style="text-align: center;width: 100%;font-size: 50px"></h1>
                </div>-->
            </div>
        </div>

        <el-dialog
                title=""
                top="10%"
                :visible.sync="showTaskVisible"
                :close-on-click-modal="false"
                :close-on-press-escape="false"
                custom-class="myDialog"
                size="tiny">
            <el-form>
                <el-form-item class="task-form" label="">{{taskDetail.name}}</el-form-item>
                <!--<el-form-item class="task-form" label="">{{taskDetail.description}}</el-form-item>-->
                <el-form-item class="task-form" label="">{{taskDetail.projectName}}</el-form-item>
                <el-form-item class="task-form" label="">{{taskDetail.stageName}}</el-form-item>
                <el-form-item class="task-form"  label=""><span v-for="item in priorityList"
                                                                    v-if="item.value == taskDetail.priority">{{item.label}}</span>
                </el-form-item>
                <el-form-item class="task-form" label="">{{taskDetail.createTime | formatDate}}</el-form-item>
                <el-form-item class="task-form" label="">{{taskDetail.endTime | formatDate}}</el-form-item>
                <el-form-item class="task-form" label="">{{taskDetail.completeTime | formatDate}}</el-form-item>
                <el-form-item label="" v-if="showTaskTime && personData">{{personData.mostTime}}</el-form-item>
                <el-form-item label="" v-if="!showTaskTime && personData">{{personData.lessTime}}</el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button v-show="showTaskTime" type="primary" @click="toTask(personData.mostTimeTaskId)"></el-button>
                <el-button v-show="!showTaskTime" type="primary" @click="toTask(personData.lessTimeTaskId)"></el-button>
            </span>
        </el-dialog>

    </div>
</template>

<script>
    import Http from '../lib/Http'
    import moment from 'moment';
    import helper from "../lib/Helper";
    export default {
        name: "SummaryNav",
        data(){
            return{

                projectTaskList:[],
                taskDetail:{},
                showTaskVisible:false,
                priorityList: [
                    {label: '', value: 0},
                    {label: '', value: 1},
                    {label: '', value: 2},
                ],
                showTaskTime:false,

                //
                whichYear1:'2018',
                personDataReqDTO:{
                    whichYear:'2018'
                },
                personData:{
                    taskNum:0,
                    taskTime:0,
                    vacationCount:0,
                    vacationTime:0,
                    singleTaskNum:0,
                    multiTaskNum:0,
                    mostTimeTaskId:'',
                    mostTimeTaskName:'',
                    mostTime:0,
                    lessTimeTaskId:'',
                    lessTimeTaskName:'',
                    lessTime:0
                },
                personTaskData:[],

                //
                // showVacationData:false,
                whichYear2:'2018',
                vacationReqDTO:{
                    whichYear:'2018'
                },
                vacationData:{
                    totalCount:0,
                    totalTime:0
                },
                vacationCountList:[],
                vacationTimeList:[],

            }
        },
        computed:{
            permit() {
                let userRole = helper.decodeToken().userRole;
                return userRole < 2;
            },
            userRole() {
                let userRole = helper.decodeToken().userRole;
                return userRole;
            }
        },
        filters: {
            formatDate: function (value) {
                if (!value) return '';
                return moment(value).format('YYYY-MM-DD');
            },
            formatTime: function (value) {
                if (!value) return '';
                return moment(value).format('YYYY-MM-DD HH:mm:ss');
            }
        },
        created(){
          this.fetchPersonData();
          this.fetchPersonEveryMonthVacation();
        },
        beforeMount(){
            this.$root.eventBus.$emit("handleTabSelected", "summaryNav");
        },
        methods:{
            //
            fetchUserList() {
                let vm = this
                Http.zsyGetHttp('/user/effective', {}, (resp) => {
                    vm.chargeManList = resp.data
                })
            },
            drawLine2(){
                // domecharts
                let myChart = this.$echarts.init(document.getElementById('myChart2'))
                // 
                myChart.setOption({
                    title: { text: '',x:'center' },
                    tooltip: {
                        trigger: 'axis',
                        axisPointer: {
                            type: 'shadow'
                        }
                    },
                    legend: {
                        left: 'left',
                        data: ['', '']
                    },
                    xAxis: {
                        data: ["","","","","","","","","","","",""]
                    },
                    yAxis: [
                        {
                            type: 'value',
                            name: '',
                            min: 0,
                            interval: 5,
                            splitLine:{
                                show:false
                            },
                        },
                        {
                            type: 'value',
                            name: '',
                            min: 0,
                            interval: 50,
                            axisLabel: {
                                formatter: '{value} h'
                            },
                            splitLine:{
                                show:false
                            },
                        }
                    ],
                    series: [
                        {
                            name: '',
                            type: 'bar',
                            barGap: 0,
                            data: this.vacationCountList
                        },
                        {
                            name: '',
                            type: 'bar',
                            data: this.vacationTimeList,
                            yAxisIndex:1
                        }
                    ]
                });
            },
            drawLine1(){
                // domecharts
                let myChart = this.$echarts.init(document.getElementById('myChart1'))
                // 
                myChart.setOption({
                    title: { text: '',x:'center' },
                    tooltip : {
                        trigger: 'item',
                        formatter: "{a} <br/>{b} : {c} ({d}%)"
                    },
                    legend: {
                        orient: 'vertical',
                        left: 'left',
                        data: ['','']
                    },
                    series : [
                        {
                            name: '',
                            type: 'pie',
                            radius : '75%',
                            center: ['50%', '60%'],
                            data:this.personTaskData,
                            itemStyle: {
                                emphasis: {
                                    shadowBlur: 10,
                                    shadowOffsetX: 0,
                                    shadowColor: 'rgba(0, 0, 0, 0.5)'
                                }
                            }
                        }
                    ]
                });
            },

            //
            fetchTaskDetail(id,num){
                Http.zsyGetHttp(`/task/detail/${id}`, {},(res)=>{
                    if (res){
                        this.taskDetail = res.data;
                        this.showTaskVisible = true;
                        if (num == 1){
                            this.showTaskTime = true;
                        } else {
                            this.showTaskTime = false;
                        }
                    }
                })
            },


            //
            toTask(taskId){
                if (taskId != null && taskId != '') {
                    this.$router.push({name: 'taskListFormComments', params: {taskId: taskId}})
                }
            },


            //
            fetchPersonData(){
                Http.zsyPostHttp('/data/annual/person-data',this.personDataReqDTO,(res)=>{
                    if (res){
                        this.personData = res.data
                        const tData = {};
                        tData.value = this.personData.multiTaskNum;
                        tData.name = '';
                        this.personTaskData.push(tData);
                        const tData2 = {};
                        tData2.value = this.personData.singleTaskNum;
                        tData2.name = '';
                        this.personTaskData.push(tData2);
                        this.drawLine1()
                    }
                })
            },
            selectPersonDataByYear(){
                if (this.whichYear1){
                    this.personDataReqDTO.whichYear = moment(this.whichYear1).format("YYYY-MM-DD")
                    this.personDataReqDTO.whichYear = this.personDataReqDTO.whichYear.substring(0,4)
                }
                this.personData = null;
                this.personTaskData = []
                this.fetchPersonData()
            },

            //
            fetchPersonEveryMonthVacation(){
                Http.zsyPostHttp('/data/annual/person-vacation/month',this.vacationReqDTO,(res)=>{
                    if (res){
                        this.vacationCountList = res.data.vacationCountList;
                        this.vacationTimeList = res.data.vacationTimeList;
                        this.vacationCountList.forEach(count =>{
                            this.vacationData.totalCount = this.vacationData.totalCount + count;
                        })
                        this.vacationTimeList.forEach(time => {
                            this.vacationData.totalTime = this.vacationData.totalTime + time;
                        })
                        this.drawLine2();
                    }
                })
            },

            selectVacationByYear(){
                if (this.whichYear2){
                    this.vacationReqDTO.whichYear = moment(this.whichYear2).format("YYYY-MM-DD")
                    this.vacationReqDTO.whichYear = this.vacationReqDTO.whichYear.substring(0,4)
                }
                this.vacationData.totalTime = 0;
                this.vacationData.totalCount = 0;
                this.vacationCountList = [];
                this.vacationTimeList = [];
                this.fetchPersonEveryMonthVacation();
            },
        }
    }
</script>

<style scoped>
    .steps {
        display: inline;
        flex-direction: column;
        margin-bottom: 20px;
        padding-bottom: 10px;
        background: #fff;
    }

    .card-title-con {
        position: relative;
        padding-left: 10px;
        margin: 0 0 16px;
        font-weight: normal;
        line-height: 40px;
        font-size: 16px;
        border-bottom: 1px solid #eee;
        color: #304156;
    }

    .steps-body {
        display: flex;
        flex-direction: row;
    }

    .stats-con {
        width: 1100px;
        font-size: 14px;
        background: #fff;
        padding: 30px 0;
        box-shadow: 0 0 10px #ccc;
        margin: auto;
        height: 500px;
    }

    .vertical {
        margin-top: 15px;
        margin-bottom: 15px;
        width: 1px;
        background: rgb(144, 147, 153);
    }

    .fb-span{
        width: 200px;
        color: black;
        font-size: 20px;
    }

    .task-span{
        font-size: 18px;
        width: 200px;
        color: black;
    }

    .my-li{
        display: flex;
        justify-content:space-between;
    }

</style>



